body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.login-container {
    margin: 0 auto;
		width: 300px; 
		position: absolute;
		top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%, 35%);
		        transform: translate(-50%, 35%);
}

.center {
  position: relative;
  top: 0; left: 0; bottom: 0; right: 0;
}

.content {
	float: left;
	width: 97%;
	margin: 0% 1.5% 1.5%;
	border-radius: 15px;
}

.content-jumbotron {
	width: 97%;
	margin: 0% 1.5% 0% 1.5%;
}

.no-padding {
	padding: 0 !important;
}

.top-bottom-padding {
	padding-top: .5em; 
	padding-bottom: .5em;	
}

.to-front {
	position: relative;
	z-index: 100;
}
.no-suggestions {
  color: #999;
  padding: 0.5rem;
}

.suggestions {
  border: 1px solid #999;
  border-top-width: 0;
  list-style: none;
  margin-top: 0;
  max-height: 11em;
  overflow-y: auto;
  padding-left: 0;
  width: calc(18.75em + 1rem);
  position:absolute;
  background-color: rgb(232,232,232);

}

.suggestions p {
  padding: 0.5rem;
  margin-bottom: 0px;
}

.suggestion-active,
.suggestions p:hover {
  background-color: #C0C0C0;
  color: #2f4f4f;
  cursor: pointer;
  font-weight: 700;
}

.suggestions p:not(:last-of-type) {
  border-bottom: 0.0625em solid #999;
}

.form-control {
  width: 20rem;
}

